body {
  background-color: #100E17 !important;
  text-decoration: none !important;
  font-family: Arial, Helvetica, sans-serif !important;
  color: white !important;
}

.myNav {
  position: relative;
  color: white;
  text-decoration: none;
  margin: 5px;
}

.myNav::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}

.myNav:hover::before {
  color: white;
  visibility: visible;
  transform: scaleX(1);
}

.intro {
  margin-top: 5rem;
}

.small {
  font-size: 12px;
}

.guitarPhoto {
  max-width: 100%;
  height: 300px;
  margin-bottom: 5rem;
  border-radius: 50%;
  box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.75);
}

.devinCastro {
  color: #104c91;
}

.blue, .navbar-toggler {
  background-color: #104c91 !important
}

.bio {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center;
}

.aboutMe {
  text-align: center;
  padding-left: 300px;
  padding-right: 300px;
}

.jumbotron {
  margin-top: -3rem;
  margin-left: 100px;
  margin-right: 100px;
  background-color: #100E17;
  border: #ffffff solid 1px;
  box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.75);
}

.logo {
  width: 50px;
  height: 50px;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.isBlue {
  color: #104c91;
}

.card {
  border-radius: 5%;
}

.projectPhoto {
  width: 100%;
  height: 300px;
  border-radius: 5%;
  box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.75);
}

.imgWrap {
  position: relative;
}

.imgWrap:hover .description {
  visibility: visible;
  border-radius: 5%;
  opacity: 1;
}

.black {
  background-color: #100E17;
}

.cardText {
  margin-top: 3rem;
}

.description {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(1, 7, 10, 0.72);
  color: #fff;
  visibility: hidden;
  opacity: 0;
  transition: opacity .08s, visibility .1s;
  -webkit-transition: opacity .08s, visibility .1s;
  -moz-transition: opacity .08s, visibility .1s;
  -ms-transition: opacity .08s, visibility .1s;
  -o-transition: opacity .08s, visibility .1s;
}

.modal-body, .modal-header, .modal-footer {
  background-color: #201b29;
  color: white;
  border: none;
}

a>img:hover {
  opacity: .5;
}

.projects {
  margin-bottom: -5rem;
}

.resume {
  background-color: #201b29;
  border-radius: 25px !important;
}

@media only screen and (max-width: 900px) {
  .aboutMe {
    padding-left: 10px;
    padding-right: 10px;
  }
  .jumbotron {
    margin-left: 30px;
    margin-right: 30px;
  }
}

@media only screen and (max-width: 500px) {
  .aboutMe {
    padding-left: 10px;
    padding-right: 10px;
  }
}